  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "cormorantgaramond-bold";
  src: url("../fonts/cormorantgaramond-bold.woff2") format("woff2"), url("../fonts/cormorantgaramond-bold.woff") format("woff");
  font-weight: 400;
