/*Colors*/
/*Viewports*/
.page {
  padding: 0;
  margin: 0;
  overflow: hidden;
  min-width: 320px;
  font-family: "Poppins-Medium", "Arial", sans-serif;
  color: #000000;
  box-sizing: border-box;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  min-height: 100vh; }

@media (max-width: 1023px) {
  .page {
    max-width: 1023px; } }

@media (max-width: 767px) {
  .page {
    max-width: 767px; } }

.page__open {
  overflow: hidden; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header__wrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column; }

.header__container {
  padding: 47px 98px 19px;
  display: flex;
  justify-content: space-between;
  padding-bottom: 16px; }

.header__form {
  position: relative;
  margin-right: 30px;
  flex-basis: 40%; }

@media (max-width: 1023px) {
  .header__form {
    display: none; } }

.header__form input {
  width: 206px;
  border: #ffffff;
  padding: 6px 0 12px 29px; }

.header__form input::placeholder {
  font-family: "poppins-medium", "Arial", sans-serif;
  font-size: 14px;
  line-height: 21px; }

.header__form input:hover {
  border-bottom: 1px solid #A87b62; }

.header__form svg {
  position: absolute;
  top: 9px; }

.header__logo {
  margin: 0 auto; }

.header a {
  text-decoration: none;
  padding: 0;
  margin: 0;
  color: #1f1f1f;
  height: min-content;
  padding-bottom: 12px; }

.header a:not(.header__logo):hover {
  border-bottom: 1px solid #A87b62; }

.header a:active {
  color: #A87b62; }

.header__users-links {
  flex-basis: 40%;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end; }

.header__cart svg {
  display: none; }

@media (max-width: 1023px) {
  .header__cart svg {
    display: block; } }

@media (max-width: 1023px) {
  .header__cart span {
    display: none; } }

@media (max-width: 1023px) {
  .header__login {
    display: none; } }

.header__users-links a {
  width: max-content;
  margin-left: 35px; }

.header__navigation {
  border-top: 1px solid #f6f5f4;
  padding-top: 18px; }

.header__navigation ul {
  list-style: none;
  padding: 0;
  margin: 0;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 80%;
  margin: 0 auto; }

.header__navigation li {
  padding: 0;
  margin: 0;
  width: max-content;
  padding-left: 35px; }

.header__toggle {
  display: none; }

@media (max-width: 1023px) {
  .header__toggle {
    display: block;
    border: none;
    background-color: #ffffff;
    background-image: url("../img/open-button.svg");
    background-repeat: no-repeat;
    width: 55px;
    height: 55px;
    flex-basis: 40%; } }

.header__additional-list {
  font-size: 18px;
  line-height: 23px;
  list-style: none;
  padding: 0;
  margin: 0;
  padding: 0;
  margin: 0;
  display: none;
  max-width: 80%; }

.header__additional-list li {
  padding-bottom: 30px; }

.header__additional-list a:not(.header__additional-special) {
  color: rgba(255, 255, 255, 0.75); }

.header--active {
  background-color: #A87b62;
  padding: 55px 45px 80px; }
  .header--active .header__container {
    flex-wrap: wrap;
    padding-right: 0;
    padding-left: 0; }
  .header--active a {
    color: #ffffff; }
  .header--active svg {
    fill: #ffffff;
    stroke: #ffffff;
    color: white; }
  .header--active .header__form {
    display: block;
    order: 3;
    flex-basis: 100%; }
  .header--active .header__form input {
    width: 100%;
    border-radius: 15px;
    background-color: rgba(255, 255, 255, 0.15); }
  .header--active .header__toggle {
    background-color: #A87b62;
    background-image: url("../img/active-button.svg"); }
  .header--active .navigation {
    border-top: none; }
  .header--active .navigation ul {
    flex-direction: column;
    border-top: none;
    margin: 0; }
  .header--active .navigation li {
    font-size: 24px;
    line-height: 36px;
    padding-bottom: 40px;
    padding-left: 0; }
  .header--active .header__additional-list {
    display: flex;
    flex-direction: column; }
  .header--active .header__additional-special {
    padding-top: 25px; }
  .header--active .header__form svg {
    left: 15px; }
